// src/components/StepCustomerInfo.js
import React, { useState } from 'react';

const StepCustomerInfo = ({ formData, updateFormData, nextStep, prevStep }) => {
  const [errorMessage, setErrorMessage] = useState(""); //  

  const handleChange = (e) => {
    updateFormData(e.target.name, e.target.value);
  };

  const handleSave = () => {
    if (!formData.name.trim()) {
      setErrorMessage("");
      return;
    }

    if (!/^\d{10,11}$/.test(formData.phoneNumber)) {
      setErrorMessage("1011");
      return;
    }

    setErrorMessage(""); // 
    console.log(":", formData);

    nextStep(); // 
  };

  return (
    <div>
      <h2 className="text-xl font-semibold mb-4"></h2>

      <div className="space-y-4">
        <div>
          <label className="block font-medium"></label>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            className="w-full border rounded p-2"
            placeholder=""
          />
        </div>
        <div>
          <label className="block font-medium"></label>
          <input
            type="tel"
            name="phoneNumber"
            value={formData.phoneNumber}
            onChange={handleChange}
            className="w-full border rounded p-2"
            placeholder="09012345678"
          />
        </div>
      </div>

      {errorMessage && (
        <p className="text-red-600 mt-2">{errorMessage}</p>
      )}

      <div className="mt-6 flex justify-between">
        <button
          onClick={prevStep}
          className="px-4 py-2 bg-gray-300 hover:bg-gray-400 text-white rounded"
        >
          
        </button>
        <button
          onClick={handleSave}
          className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded"
        >
          
        </button>
      </div>
    </div>
  );
};

export default StepCustomerInfo;
