// src/components/StepConfirm.js
import React from 'react';
import { useNavigate } from 'react-router-dom';

const StepConfirm = ({ formData, prevStep }) => {
  const navigate = useNavigate();

  const handleSubmit = async () => {
    try {
      const response = await fetch('http://localhost:4000/api/reservations', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          menu: formData.menu,
          date: formData.date,
          time: formData.time,
          therapist: formData.therapist,
          customerName: formData.name,
          phoneNumber: formData.phoneNumber,
          note: formData.note,
        }),
      });

      if (!response.ok) {
        throw new Error('');
      }

      console.log(' ');
      navigate('/reservation/thanks');
    } catch (error) {
      console.error(' :', error);
      alert('');
    }
  };

  return (
    <div>
      <h2 className="text-xl font-semibold mb-4"></h2>

      <div className="space-y-4 text-gray-800">
        <p><strong></strong> {formData.menu}</p>
        <p><strong></strong> {formData.date}</p>
        <p><strong></strong> {formData.time}</p>
        <p><strong></strong> {formData.therapist}</p>
        <p><strong></strong> {formData.name}</p>
        <p><strong></strong> {formData.phoneNumber}</p>
        <p><strong></strong> {formData.note || ''}</p>
      </div>

      <div className="mt-6 flex justify-between">
        <button
          onClick={prevStep}
          className="px-4 py-2 bg-gray-300 hover:bg-gray-400 text-white rounded"
        >
          
        </button>
        <button
          onClick={handleSubmit}
          className="px-4 py-2 bg-green-600 hover:bg-green-700 text-white rounded"
        >
          
        </button>
      </div>
    </div>
  );
};

export default StepConfirm;
