// src/pages/AdminReservationList.js
import React, { useEffect, useState } from "react";

const AdminReservationList = () => {
const [reservations, setReservations] = useState([]);
const [loading, setLoading] = useState(true);
const [editingReservation, setEditingReservation] = useState(null);
const [editForm, setEditForm] = useState({});

  // 
const fetchReservations = () => {
    fetch("http://localhost:4000/api/reservations")
    .then((res) => res.json())
    .then((data) => {
        setReservations(data);
        setLoading(false);
    })
    .catch((error) => {
        console.error(":", error);
        setLoading(false);
    });
};

useEffect(() => {
    fetchReservations();
}, []);

const handleEditClick = (rsv) => {
    setEditForm({
        menu: rsv.menu,
        date: rsv.date,
        time: rsv.time,
        therapist: rsv.therapist,
        customerName: rsv.customer_name,
        phoneNumber: rsv.phone_number,
        note: rsv.note,
        });
    console.log("rsv:", rsv);
    setEditingReservation(Number(rsv.id)); // number
     //  date rsv.datenull
    const formattedDate = rsv.date
        ? new Date(rsv.date).toISOString().split("T")[0]
        : "";
    setEditForm({
        ...rsv,
        date: formattedDate, 
    });
};

const handleChange = (e) => {
const { name, value } = e.target;
console.log(":", name, value);
    setEditForm((prev) => ({ ...prev, [name]: value }));
};

const handleUpdate = async () => {
    try {
        console.log(":", editForm);
        const res = await fetch(
        `http://localhost:4000/api/reservations/${editingReservation}`,
        {
            method: "PUT",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify(editForm),
        }
        );
        const data = await res.json();
        console.log(":", data);

        setReservations((prev) => {
            const updated = prev.map((r) =>
                r.id === editingReservation ? { ...r, ...editForm } : r
            );
            console.log(":", updated);
            return updated;
        });

        await fetchReservations(); // 

        setEditingReservation(null);
        alert("");
    } catch (err) {
        console.error(":", err);
        alert("");
    }
};

const handleDelete = async (id) => {
    if (!window.confirm("")) return;

    try {
        const res = await fetch(`http://localhost:4000/api/reservations/${id}`, {
        method: "DELETE",
        });

        if (!res.ok) throw new Error("");
        console.log(':', res.status);


        setReservations((prev) => prev.filter((r) => r.id !== id));
        alert("");
        } catch (err) {
        console.error(":", err);
        alert("");
    }
};

if (loading) return <p>...</p>;

return (
    <div className="p-4">
        <h2 className="text-xl font-semibold mb-4"></h2>

        {reservations.length === 0 ? (
        <p className="text-gray-500"></p>
        ) : (
        <ul className="space-y-4">
            {reservations.map((rsv) => (
            <li key={rsv.id} className="p-4 border rounded shadow">
                {editingReservation === rsv.id ? (
                <div className="space-y-2">
                    <label className="block text-sm font-semibold">
                    
                    </label>
                    <input
                    type="text"
                    name="menu"
                    value={editForm.menu || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold"></label>
                    <input
                    type="date"
                    name="date"
                    value={
                        editForm.date
                        ? new Date(editForm.date).toISOString().split("T")[0]
                        : ""
                    }
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold"></label>
                    <input
                    type="time"
                    name="time"
                    value={editForm.time || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold">
                    
                    </label>
                    <input
                    type="text"
                    name="therapist"
                    value={editForm.therapist || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold">
                    
                    </label>
                    <input
                    type="text"
                    name="customerName"
                    value={editForm.customerName || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold">
                    
                    </label>
                    <input
                    type="text"
                    name="phoneNumber"
                    value={editForm.phoneNumber || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <label className="block text-sm font-semibold"></label>
                    <textarea
                    name="note"
                    value={editForm.note || ""}
                    onChange={handleChange}
                    className="border p-1 rounded w-full"
                    />

                    <div className="flex justify-between items-center mt-4">
                        <div className="flex space-x-2">
                            <button
                                onClick={handleUpdate}
                                className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700"
                            >
                            
                            </button>
                            <button
                                onClick={() => setEditingReservation(null)}
                                className="px-4 py-2 bg-gray-400 text-white rounded hover:bg-gray-500"
                            >
                            
                            </button>
                        </div>
                        <button
                            onClick={() => handleDelete(rsv.id)}
                            className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
                        >
                        
                        </button>
                    </div>
                </div>
                ) : (
                <div>
                    <p>
                    <strong></strong> {rsv.menu}
                    </p>
                    <p>
                    <strong></strong> {rsv.date} {rsv.time}
                    </p>
                    <p>
                    <strong></strong> {rsv.therapist}
                    </p>
                    <p>
                    <strong></strong> {rsv.customerName}
                    </p>
                    <p>
                    <strong></strong> {rsv.phoneNumber}
                    </p>
                    <p>
                    <strong></strong> {rsv.note || ""}
                    </p>
                    <div className="flex justify-between mt-2">
                        <button
                            onClick={() => handleEditClick(rsv)}
                            className="mt-2 px-3 py-1 bg-blue-600 text-white rounded hover:bg-blue-700"
                        >
                        
                        </button>
                        <button
                            onClick={() => handleDelete(rsv.id)}
                            className="mt-2 ml-2 px-3 py-1 bg-red-600 text-white rounded hover:bg-red-700"
                        >
                        
                        </button>
                    </div>
                </div>
                )}
            </li>
            ))}
        </ul>
        )}
    </div>
);
};

export default AdminReservationList;
